import React from 'react';
import { AboutVDE } from './about-vde/about-vde.component';
import { OurValues } from './our-values/our-values.component';
import { Sharing } from './sharing/sharing.component';
import { CSSTransition } from 'react-transition-group';
import { SectionsContainer, Section } from 'react-fullpage';
import { Header } from '../../ui/mobile-header/header.component';

const Animate = ({ transitionIn, transition = 'fade', ...props }) => (
  <CSSTransition
    in={transitionIn}
    unmountOnExit
    timeout={2000}
    classNames={transition}
  >
    <div className={transition} {...props} />
  </CSSTransition>
);

export const AboutContainer = () => {
  const options = {
    sectionClassName: 'section',
    anchors: ['sectionOne', 'sectionTwo', 'sectionThree'],
    scrollBar: false,
    navigation: true,
    verticalAlign: false,
    sectionPaddingTop: '50px',
    sectionPaddingBottom: '50px',
  };

  return (
    <SectionsContainer {...options}>
      <Section>
        <AboutVDE />
      </Section>
      <Section>
        <OurValues />
      </Section>
      <Section>
        <Sharing />
      </Section>
    </SectionsContainer>
  );
};
